:root {
  --bg-color: #F7F7D4;
  --content-bg-color: #F8E9D8;
  --accent-color: #B2D732;
  --text-color: #1B3409;
}
* {
  overflow-x: hidden
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background-color: var(--bg-color);
  background-image: repeating-linear-gradient(45deg, var(--bg-color) 25%, transparent 25%, transparent 75%, var(--bg-color) 75%, var(--bg-color)), 
                    repeating-linear-gradient(45deg, var(--bg-color) 25%, var(--content-bg-color) 25%, var(--content-bg-color) 75%, var(--bg-color) 75%, var(--bg-color));
  background-position: 0 0, 10px 10px;
  background-size: 20px 20px;
}

header, footer {
  width: 100%;
  text-align: center;
  overflow:visible;
}

header {
  position: absolute;
  top: 0;
}

footer {
  margin-top: auto;
  background-color: var(--accent-color);
  border-radius: 10px 10px 0 0;
}

main {
  padding: 10px 10px 10px 10px;
  width:90%;
  max-width: 800px;
  min-height: 80vh;
  margin: auto;
  margin-top: 80px;
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: var(--content-bg-color);
  border-radius: 10px;
  border: 4px dotted var(--bg-color);
}

h1, h2, p, li {
  margin: 10px 0;
  color: var(--text-color);
}

footer > p {
  font-size: 0.8em;
  opacity: 0.7;
}

ul {
  list-style: none;
  border: 2px solid var(--accent-color);
  border-radius: 10px;
  padding: 0 10px;
}

.header-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  background: var(--accent-color);
  border-radius: 0 0 10px 10px;
  outline: var(--accent-color) solid 2px;
  background-image: linear-gradient(30deg, var(--bg-color) 12%, transparent 12.5%, transparent 87%, var(--bg-color) 87.5%, var(--bg-color)), 
                    linear-gradient(150deg, var(--bg-color) 12%, transparent 12.5%, transparent 87%, var(--bg-color) 87.5%, var(--bg-color)), 
                    linear-gradient(30deg, var(--bg-color) 12%, transparent 12.5%, transparent 87%, var(--bg-color) 87.5%, var(--bg-color)), 
                    linear-gradient(150deg, var(--bg-color) 12%, transparent 12.5%, transparent 87%, var(--bg-color) 87.5%, var(--bg-color)), 
                    linear-gradient(60deg, var(--bg-color) 25%, transparent 25.5%, transparent 75%, var(--bg-color) 75%, var(--bg-color)), 
                    linear-gradient(60deg, var(--bg-color) 25%, transparent 25.5%, transparent 75%, var(--bg-color) 75%, var(--bg-color));
  background-size: 20px 35px;
  background-position: 0 0, 0 0, 10px 18px, 10px 18px, 0 0, 10px 18px;
}

.header-text h1 {
  padding: 0 12px 1px;
  font-size: 2em;
  background-color: var(--accent-color);
  border-radius: 20px;
  border: 2px solid var(--bg-color);
}

.header-image {
  position: absolute;
  top: -10px;
  right: -10px;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  border: 3px solid var(--bg-color);
  transition: width 0.5s, height 0.5s, top 0.5s, right 0.5s;
}

.header-image:hover {
  top: 10px;
  right: 20px;
  width: 200px;
  height: 200px;
  border: 3px solid var(--accent-color);
}

h2 {
  cursor: pointer;
}